/* =====================================================
   MOBILE USER MENU - SIMPLE Y FUNCIONAL
   ===================================================== */

.mobile-user-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-xs);
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* =====================================================
   INFORMACIN DEL USUARIO
   ===================================================== */

.mobile-user-info {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  background: var(--color-bg-surface);
  border: 1px solid var(--color-border-subtle);
  border-radius: var(--radius-md);
  padding: var(--space-sm) var(--space-md);
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  margin: 0;
}

.mobile-user-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #ffd700, #ffa500);
  border: 2px solid rgba(255, 215, 0, 0.3);
  flex-shrink: 0;
}

.mobile-user-avatar-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.mobile-user-avatar-initials {
  color: #ffffff;
  font-weight: 600;
  font-size: 14px;
  text-shadow: none;
}

.mobile-user-details {
  display: flex;
  flex-direction: column;
  gap: 2px;
  flex: 1;
  min-width: 0;
}

.mobile-user-name {
  color: #ffffff;
  font-weight: 600;
  font-size: 14px;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.mobile-user-email {
  color: #a0a0a0;
  font-size: 11px;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* =====================================================
   OPCIONES DEL MEN
   ===================================================== */

.mobile-menu-options {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.mobile-menu-option {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--space-sm);
  background: var(--color-bg-surface);
  border: 1px solid var(--color-border-subtle);
  color: var(--color-text-primary);
  font-size: 1rem;
  font-weight: var(--font-weight-semibold);
  padding: var(--space-md) var(--space-lg);
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: all var(--transition-base);
  text-align: center;
  width: 100%;
  max-width: 100%;
  min-width: 0;
  box-sizing: border-box;
  margin: 0 auto;
}

.mobile-menu-option:hover {
  background: rgba(255, 215, 0, 0.2);
  border-color: rgba(255, 215, 0, 0.4);
  color: #ffd700;
  transform: translateX(3px);
}

.mobile-menu-icon {
  font-size: 1.25rem;
  width: auto;
  text-align: center;
  flex-shrink: 0;
}

.mobile-menu-text {
  flex: 1;
}

.mobile-menu-divider {
  border: none;
  border-top: 1px solid rgba(255, 215, 0, 0.2);
  margin: 8px 0;
}

.mobile-menu-admin {
  background: rgba(0, 212, 255, 0.1);
  border-color: rgba(0, 212, 255, 0.3);
  color: #00d4ff;
  font-weight: 600;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  margin: 0;
}

.mobile-menu-admin:hover {
  background: rgba(0, 212, 255, 0.2);
  border-color: rgba(0, 212, 255, 0.5);
  color: #00b8e6;
}

.mobile-menu-logout {
  background: var(--color-bg-surface);
  border: 2px solid rgba(220, 38, 38, 0.5);
  color: rgba(220, 38, 38, 1);
  font-weight: var(--font-weight-semibold);
  font-size: 1.0625rem;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: var(--space-md) var(--space-xl);
}

.mobile-menu-logout:hover {
  background: rgba(220, 38, 38, 0.15);
  border-color: rgba(220, 38, 38, 0.8);
  color: rgba(220, 38, 38, 1);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(220, 38, 38, 0.2);
}

/* =====================================================
   RESPONSIVE
   ===================================================== */

@media (max-width: 480px) {
  .mobile-user-menu {
    width: 100%;
    max-width: 100%;
    gap: var(--space-sm);
    padding: 0;
    margin: 0;
  }

  .mobile-user-info {
    padding: var(--space-sm) var(--space-md);
  }

  .mobile-menu-option {
    padding: var(--space-md) var(--space-lg);
    font-size: 0.9375rem;
  }

  .mobile-menu-logout {
    font-size: 1rem;
    padding: var(--space-md) var(--space-lg);
  }

  .mobile-menu-icon {
    font-size: 1.125rem;
  }
}

/* iPhone 17 Pro Max y pantallas mviles grandes (430px - 480px) */
@media (max-width: 480px) and (min-width: 430px) {
  .mobile-user-menu {
    padding: 0 10px;
    margin: 0 -20px;
  }

  .mobile-user-info {
    padding: 6px 8px;
  }

  .mobile-user-name {
    font-size: 13px;
  }

  .mobile-user-email {
    font-size: 10px;
  }
}

/* Pantallas mviles extra grandes (iPhone Pro Max) */
@media (max-width: 430px) {
  .mobile-user-menu {
    padding: 0 8px;
    margin: 0 -16px;
  }

  .mobile-user-info {
    padding: 5px 6px;
  }

  .mobile-user-name {
    font-size: 12px;
  }

  .mobile-user-email {
    font-size: 9px;
  }

  .mobile-user-info {
    padding: 6px 10px;
    width: 100%;
    max-width: 100%;
  }

  .mobile-user-avatar {
    width: 28px;
    height: 28px;
  }

  .mobile-user-avatar-initials {
    font-size: 12px;
  }

  .mobile-user-name {
    font-size: 12px;
  }

  .mobile-user-email {
    font-size: 10px;
  }

  .mobile-menu-options {
    gap: 4px;
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }

  .mobile-menu-option {
    padding: 8px 2px;
    font-size: 10px;
  }

  .mobile-menu-icon {
    font-size: 12px;
    width: 14px;
  }
}
